import { Component, Input, OnInit } from '@angular/core';
import { productsUrl } from 'src/app/config/apiurl';
import { Product } from 'src/app/models/product';
import { CartserviceService } from 'src/app/services/cartservice.service';
import { MessengerserviceService } from 'src/app/services/messengerservice.service';


@Component({
  selector: 'app-productitem',
  templateUrl: './productitem.component.html',
  styleUrls: ['./productitem.component.scss']
})
export class ProductitemComponent implements OnInit {

@Input() productlistvar : Product

// addTowishlist :boolean = false;

  constructor(private messengerservice : MessengerserviceService,
    private cartitemservice :CartserviceService,
    ) { }

  ngOnInit(): void {
  }
handleaddtocart()
{
  this.cartitemservice.addProductToCartItem(this.productlistvar).subscribe
  ( () =>
  {
    this.messengerservice.sendMsg(this.productlistvar);
  }
  )
  
}



}
